.table {
  margin-top: 20px;
  overflow: scroll;
  scroll-behavior: smooth;
  height: 400px;
  color: black;
  overflow-x: hidden;
}

.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.5rem;
}
